import React, { useRef, useEffect } from 'react'

import AboutIcon from '../../svg-components/about-icon'

import './about.scss'

const About = ({ setAbout }) => {
  const ref = useRef()

  useEffect(() => {
    setAbout(ref)
  }, [setAbout])

  return (
    <div className='about' ref={ref}>
      <div className='top-bar'>
        <div className='about-header'>
          <p className='section-title'>ABOUT</p>
          <AboutIcon className='about-icon' fill='#FFF' />
        </div>
        <div className='line-break'></div>
      </div>
      <div className='text-container'>
        <p>I've had an unusual though rewarding life path that has led me to where I am today. From age 12 to 26 I was a music industry professional, first as a full time studio and touring musician, and later as a producer and engineer. I first started messing around with websites in the early '90s, but it was out of necessity that I started building Flash based sites and other digital presence solutions for mine and other bands in my area, as well as local tattoo shops, galleries, drag events, recording studios, and other clients in the more creative industries. At this time was also my introduction to JavaScript and PHP.
          </p>
        <p>Eventually the music took over and my development took a backseat, but audio engineering is essentially data flow management and manipulation after all. After a decade of moderate success in music, and living through the death of the pro studio industry, I sold all of my belongings and moved to Istanbul for a new start.</p>
        <p>I picked up a few small audio related gigs in Istanbul in an effort to continue in music, but quickly found the industry to be too lacking to be sustainable as a (foreign) freelancer. I ended up picking up a full time job doing everything from print and digital design, book keeping, and client relations to Excel and Python automations for a large consumer electronics manufacturer. This would be my first time learning programming for productivity. After some time here I ended up hanging on the Amalfi coast and exploring the rest of Europe on my savings.</p>
        <p>After a few years abroad it was time to return state side, San Francisco sounded nice. I needed a job and slipped into a bartending position. Some work related injuries (bartending is crazy y'all) and a pandemic later and I decided to change gears.</p>
        <p>I jumped into the deep end of modern JS, frontend and backend frameworks, CS fundamentals, functional programming, OOP, DevOps, and more, and essentially boot-camped myself. I started building things, breaking others, and learned as much as possible along the way.</p>
        <p>In my free time I'm focused on selective freelancing for interesting clients and using personal projects to learn new technologies, and of course building my career in development.</p>
      </div>
    </div>
  )
}

export default About