import React, { useState, useEffect, useRef } from 'react'
import ScrollSnap from 'scroll-snap'
import smoothscroll from 'smoothscroll-polyfill'
import { CSSTransition } from 'react-transition-group'
import useHorizontal from '@oberon-amsterdam/horizontal/hook'
import useWindowDimensions from '../../hooks/useWindowDimensions'
import { useMediaPredicate } from 'react-media-hook'

import EasingFunctions from '../../global/EasingFunctions'

import Header from '../header/header'
import Logo from '../../svg-components/logo'
import Menu from '../menu/menu'
import Main from '../main/main'
import CustomButton from '../custom-button/custom-button'
import RotateIcon from '../../svg-components/rotate-icon'

import Home from '../../pages/home/home'
import About from '../../pages/about/about'
import Work from '../../pages/work/work'
// import Blog from '../../pages/blog/blog'
import Contact from '../../pages/contact/contact'

import './container.scss'

smoothscroll.polyfill()

const Container = () => {
  const { width, height } = useWindowDimensions()
  const [burgerSize, setBurger] = useState(20)
  const [isOpen, setOpen] = useState(false)
  const isMobile = useMediaPredicate('(max-width: 768px)')
  const scrollRef = useRef()

  useHorizontal({ container: scrollRef.current });

  const [home, setHome] = useState()
  const [about, setAbout] = useState()
  const [work, setWork] = useState()
  // const [blog, setBlog] = useState()
  const [contact, setContact] = useState()

  const mainStyle = {
    height: height
  }

  const handleScroll = (page) => {
    page.current.scrollIntoView({ behavior: 'smooth' })
  }

  useEffect(() => {
    if (width < 375) {
      setBurger(15)
    } else if (width < 500) {
      setBurger(20)
    }
    if (width > 769) {
      setOpen(true)
    } else {
      setOpen(false)
    }
  }, [width])

  useEffect(() => {
    const element = scrollRef.current
    const snapElement = new ScrollSnap(element, {
      snapDestinationX: '100vw',
      timeout: 10,
      duration: 230,
      threshold: .2,
      easing: EasingFunctions.easeInOutQuad
    })
    snapElement.bind()
  })

  return (
    <div className='main-container' style={mainStyle}>
      <Header
        isOpen={isOpen}
        setOpen={setOpen}
        width={width}
        burgerSize={burgerSize}
        setBurger={setBurger}
        handleScroll={handleScroll}
        home={home}
      >
        <Logo onClick={() => handleScroll(home)} className='logo' />
        <h1 onClick={() => handleScroll(home)} className='title'>R Y A N â€ƒ M A G D A L E N O</h1>
        {
          isMobile ?
            <CSSTransition
              in={isOpen}
              timeout={3000}
              classNames='menu-transitions'
              unmountOnExit
            >
              <Menu>
                <CustomButton
                  onClick={() => {
                    handleScroll(about)
                    setOpen(false)
                  }}
                  className='custom-button menu-button'>
                  <span>A B O U T</span>
                </CustomButton>
                <CustomButton
                  onClick={() => {
                    handleScroll(work)
                    setOpen(false)
                  }}
                  className='custom-button menu-button'>
                  <span>W O R K</span>
                </CustomButton>
                {/* <CustomButton
                onClick={() => handleScroll(blog)}
                className='custom-button menu-button'>
                <span>B L O G</span>
              </CustomButton> */}
                <CustomButton
                  onClick={() => {
                    handleScroll(contact)
                    setOpen(false)
                  }}
                  className='custom-button menu-button'>
                  <span>C O N T A C T</span>
                </CustomButton>
              </Menu>
            </CSSTransition>
            :
            <Menu>
              <CustomButton
                onClick={() => {
                  handleScroll(about)
                  setOpen(false)
                }}
                className='custom-button menu-button'>
                <span>A B O U T</span>
              </CustomButton>
              <CustomButton
                onClick={() => {
                  handleScroll(work)
                  setOpen(false)
                }}
                className='custom-button menu-button'>
                <span>W O R K</span>
              </CustomButton>
              {/* <CustomButton
                onClick={() => handleScroll(blog)}
                className='custom-button menu-button'>
                <span>B L O G</span>
              </CustomButton> */}
              <CustomButton
                onClick={() => {
                  handleScroll(contact)
                  setOpen(false)
                }}
                className='custom-button menu-button'>
                <span>C O N T A C T</span>
              </CustomButton>
            </Menu>
        }
      </Header>
      <div className='page-container'>
        <Main
          ref={scrollRef}>
          <Home setHome={setHome} handleScroll={handleScroll} about={about} />
          <About setAbout={setAbout} />
          <Work setWork={setWork} />
          {/* <Blog setBlog={setBlog} /> */}
          <Contact setContact={setContact} />
        </Main>
        <div className='shadow'></div>
        <div className='shadow2'></div>
      </div>
      {
        width > height && width < 820 &&
        <div className='rotate'>
          <RotateIcon className='rotate-icon' fill='rgba(255, 0, 255, 0.9)' />
          <p>Please rotate your device for the best viewing experience!</p>
        </div>
      }
    </div>
  )
}

export default Container