import React, { useState } from 'react'
import { CSSTransition } from 'react-transition-group'
import { useMediaPredicate } from 'react-media-hook'

import CustomButton from '../custom-button/custom-button'

import './project-card.scss'

const ProjectCard = ({ project }) => {
  const [showDescription, setShow] = useState(false)
  const isMobile = useMediaPredicate('(min-width: 768px)')

  const {
    title,
    shortDescription,
    photo,
    video,
    description,
    link,
  } = project.fields

  const photoLink = photo && `https:${photo.fields.file.url}`
  const videoLink = video && `https:${video.fields.file.url}`

  return (
    <div className='project-card'>
      {
        !isMobile ?
          <>
            <h2>{title}</h2>
            {
              video ?
                <video loop controls={false} autoPlay={true} width='100%'>
                  <source src={videoLink} />
                </video> :
                <img alt='' src={photoLink} />
            }
            <p>{shortDescription}</p>
            <CustomButton
              className='custom-button project-button'
              onClick={() => setShow(!showDescription)}>
              {
                !showDescription ? 'M O R E' : 'C L O S E'
              }
            </CustomButton>
            <CSSTransition
              in={showDescription}
              timeout={100}
              classNames='desc-transition'
              unmountOnExit
            >
              <div className='desc-container'>
                <p>{description}</p>
              </div>
            </CSSTransition>
            <CustomButton className='custom-button project-button'><a href={`https://${link}`}>V I S I T</a></CustomButton>
          </>
          :
          <div className='desktop'>
            <div className='left'>
              <h2>{title}</h2>
              {
                video ?
                  <video loop controls={false} autoPlay={true} width='100%'>
                    <source src={videoLink} />
                  </video> :
                  <img alt='' src={photoLink} />
              }
            </div>
            <div className='right'>
              <p>{description}</p>
              <CustomButton className='custom-button project-button'><a target='blank' href={`https://${link}`}>V I S I T</a></CustomButton>
            </div>
          </div>
      }
    </div>
  )
}

export default ProjectCard