import React, { useState, useEffect, useRef } from 'react'

import useFreelance from '../../apis/contentful/use-freelance'
import usePersonal from '../../apis/contentful/use-personal'

import Portfolio from '../../components/portfolio/portfolio'
import CustomButton from '../../components/custom-button/custom-button'
import WorkIcon from '../../svg-components/work-icon'
import Spinner from '../../components/spinner/spinner'

import './work.scss'

const Work = ({ setWork }) => {
  const [freelance, freelanceLoading] = useFreelance()
  const [personal, personalLoading] = usePersonal()
  const [category, setCategory] = useState(false)
  const personalStyle = category ? 'selected' : null
  const freelanceStyle = !category ? 'selected' : null

  const ref = useRef()

  useEffect(() => {
    setWork(ref)
  }, [setWork])

  return (
    <div className='work' ref={ref}>
      <div className='top-bar'>
        <div className='work-header'>
          <WorkIcon className='work-icon' fill='#FFF' />
          <p className='section-title'>WORK</p>
        </div>
        <div className='line-break'></div>
      </div>
      <div className='button-container'>
        <CustomButton
          onClick={() => setCategory(false)}
          className={`custom-button toggle-button ${freelanceStyle}`}>
          Freelance
          </CustomButton>
        <CustomButton
          onClick={() => setCategory(true)}
          className={`custom-button toggle-button ${personalStyle}`}>
          Personal
          </CustomButton>
      </div>
      {
        freelanceLoading || personalLoading ? <Spinner /> :
          <div className='portfolio-container'>
            {
              !category ? <Portfolio projects={freelance}/> : <Portfolio projects={personal}/>
            }
          </div>
      }
    </div>
  )
}

export default Work